<template>
  <div>
    <base target="alink"/>
    <div>
      <ul>访问二级路由
        <li>想要显示二级路由必须在一级路由中添加router-view,</li>
        <li>若想通过/question->question0访问二级路由/question/question0必须先打开/question</li>
        <li>也可直接访问/question/question0</li>
        <li><a href="https://www.cnblogs.com/xsphehe/p/6938438.html">iframe 置空（原因未知）</a></li>
        <li><a href="javascript:void(0);">界面不动</a></li>
      </ul>
    </div>
    <!--<ul>-->
      <!--<router-link to="question0" tag="li" exact>-->
        <!--<div>question0</div>-->
      <!--</router-link>-->
      <!--<router-link to="question1" tag="li" exact>-->
        <!--<div>question1</div>-->
      <!--</router-link>-->
    <!--</ul>-->
    <router-view></router-view>
      <iframe  src="../../../static/img/test.jpg" name="alink"></iframe>
  </div>
</template>

<script>

export default {
  name: 'question'
}

</script>

<style scoped>
  iframe{
    width: 50%;
    height: 700px;
    position:fixed;
    top:120px;
    right: 35px;
    border:none;
  }
</style>



